import { Collapse } from 'bootstrap';
import './button.css'
import { useState } from 'react'
import { act } from 'react-dom/test-utils';

export default function Button()
{
    const [showactivity,setshowactivity]=useState(false);
    const[allactivity,setallactivity]=useState([])
    const [activity,setactivity]=useState([])
  //  const[show,setshow]=useState(false)
    const [expandedIndex, setExpandedIndex] = useState(null);
    
 

    function Activity()
    {
        setallactivity([])
       fetch('https://www.boredapi.com/api/activity')
       .then((response)=>
       {
        return response.json()  ;
       }).then((result)=>
       {
        setactivity([result])
        setallactivity([...allactivity,result]);
       
        
       })
            setshowactivity(true)
       
    }
    function description(index)
    {
        if (expandedIndex === index) {
            setExpandedIndex(null);
          } else {
            setExpandedIndex(index);
          }
    }


  
    
    
    return(
        <div>
           <div className='d-flex justify-content-center mt-5'>
            <button className="btn btn-danger" onClick={Activity}>Generate Activity</button>
            </div>
          
          {showactivity &&
            <div className='show '>
            <ul  className='mt-5' >
          
            
            {allactivity.map((ele,index)=>
                <div>
                     <div className='d-flex mt-5'>
                     <li>{ele.activity}</li><button className='btn btn-primary  expand' onClick={() => description(index)}>
                    {expandedIndex === index ? 'Collapse' : 'Expand'}</button>
                    
                </div>
                {expandedIndex === index && (
                  <ul style={{ display: 'block' }}>
                    <li>Type: {ele.type}</li>
                    <li>Participants: {ele.participants}</li>
                    <li>Price: {ele.price}</li>
                    <li>Key: {ele.key}</li>
                    <li>Accessibility: {ele.accessibility}</li>
                  </ul>
                )}
                
               
                </div>
               
               
            )
            }
            
             
             
          
            </ul>
            
            
            </div>
            }

           
            
               
            </div>
           
          
        
     
    )
  
}